import React, { Component } from 'react';

import NavLink from './NavLink';

class Back extends Component{
	render() {
		return(
			<div class="button_black">
				<i class="fa fa-angle-left" aria-hidden="true"></i>
				
			</div>
		)
	}
}

class Quest1 extends Component{

	render() {
		return(
			<div className="right_info">
				<div className="button_right_wrap">
					<NavLink to="/user_user/quest/personal-info" location={this.props.location}>1.  </NavLink>
					<NavLink to="/user_user/quest/passport" location={this.props.location}>2.  </NavLink>
					<NavLink to="/user_user/quest/info-family" location={this.props.location}>3.   </NavLink>
					<NavLink to="/user_user/quest/education" location={this.props.location}>4. </NavLink>
					<NavLink to="/user_user/quest/contacts" location={this.props.location}>5. </NavLink>
					<NavLink to="/user_user/quest/arrival" location={this.props.location}>6.   </NavLink>
				</div>
				<h3>   ?</h3>
				<p><a href=""> </a>  <a href=""> </a>           .</p>
			</div>
		)
	}
}

class StandartRight extends Component{
	render() {
		return(
			<div className="right_info">
				<h3>    </h3>
				<p><a href=""> </a>  <a href=""> </a>    .</p>
			</div>
		)
	}
}

export default class RightSidebar extends Component{
	render() {

		let rightSidebar;
		if(this.props.params.quest) rightSidebar = <Quest1 location={this.props.location} />
		else if(this.props.params.add || this.props.params.table) rightSidebar = <Back />
		else rightSidebar = <StandartRight />

		return(
			React.cloneElement(rightSidebar)
		)
	}
}